import { styled, Box } from '@material-ui/core'
import BookmarkIcon from '@material-ui/icons/Bookmark'
import SearchIcon from '@material-ui/icons/Search'
import FavoriteIcon from '@material-ui/icons/Favorite'
import AccountCircleIcon from '@material-ui/icons/AccountCircle'
import CameraEnhanceIcon from '@material-ui/icons/CameraEnhance'

export const NavigationBarContainer = styled(Box)({
  display: 'flex',
  justifyContent: 'space-around',
  alignItems: 'center',
  background: '#B92626',
  height: '87px',
  position: 'fixed',
  margin: '0px',
  bottom: '0',
  width: '100vw',
  zIndex: '1'
})

export const BookmarkIconNav = styled(BookmarkIcon)({
  fontSize: '45px'
})

export const SearchIconNav = styled(SearchIcon)({
  fontSize: '45px'
})

export const FavoriteIconNav = styled(FavoriteIcon)({
  fontSize: '45px'
})

export const AccountIconNav = styled(AccountCircleIcon)({
  fontSize: '45px'
})

export const CameraIconNav = styled(CameraEnhanceIcon)({
  color: '#B92626',
  border: '5px solid #B92626',
  fontSize: '45px',
  backgroundColor: 'white',
  padding: '15px',
  borderRadius: '100px',
  marginBottom: '5vh'
})

export const NavItem = styled(Box)({
  display: 'flex',
  flexDirection: 'column',
  opacity: '0.8',
  alignItems: 'center',
  color: 'white',
  paddingBottom: '20px'
})

export const SelectedNavItem = styled(Box)({
  display: 'flex',
  flexDirection: 'column',
  alignItems: 'center',
  color: 'white',
  paddingBottom: '20px'
})

export const Underline = styled(Box)({
  borderBottom: '5px solid white'
})
