import React, { useState } from 'react'
import { Link } from 'react-router-dom'
import {
  NavigationBarContainer,
  BookmarkIconNav,
  SearchIconNav,
  FavoriteIconNav,
  AccountIconNav,
  CameraIconNav,
  NavItem,
  SelectedNavItem,
  Underline
} from './Styles'

const NavigationBar = () => {
  const [selectedItem, setSelectedItem] = useState('camera')

  if (selectedItem === '') {
    return (
      <NavigationBarContainer>
        <Link to='/searchbar'> <SelectedNavItem onClick={() => setSelectedItem('saved')}><BookmarkIconNav />Saved</SelectedNavItem></Link>
        <Link to='/searchbar'><SelectedNavItem onClick={() => setSelectedItem('search')}><SearchIconNav />Search</SelectedNavItem></Link>
        <Link to='/searchcamera'><SelectedNavItem onClick={() => setSelectedItem('camera')}><CameraIconNav /></SelectedNavItem></Link>
        <SelectedNavItem onClick={() => setSelectedItem('popular')}><FavoriteIconNav />Popular</SelectedNavItem>
        <SelectedNavItem onClick={() => setSelectedItem('profile')}><AccountIconNav />Profile</SelectedNavItem>
      </ NavigationBarContainer>
    )
  } else if (selectedItem === 'saved') {
    return (
      <NavigationBarContainer>
        <Underline><Link to='/searchbar'><SelectedNavItem onClick={() => setSelectedItem('saved')}><BookmarkIconNav />Saved</SelectedNavItem></Link></Underline>
        <Link to='/searchbar'><NavItem onClick={() => setSelectedItem('search')}><SearchIconNav />Search</NavItem></Link>
        <Link to='/searchcamera'><NavItem onClick={() => setSelectedItem('camera')}><CameraIconNav /></NavItem></Link>
        <NavItem onClick={() => setSelectedItem('popular')}><FavoriteIconNav />Popular</NavItem>
        <NavItem onClick={() => setSelectedItem('profile')}><AccountIconNav />Profile</NavItem>
      </ NavigationBarContainer>
    )
  } else if (selectedItem === 'search') {
    return (
      <NavigationBarContainer>
        <Link to='/searchbar'><NavItem onClick={() => setSelectedItem('saved')}><BookmarkIconNav />Saved</NavItem></Link>
        <Link to='/searchbar'><Underline><SelectedNavItem onClick={() => setSelectedItem('search')}><SearchIconNav />Search</SelectedNavItem></Underline></Link>
        <Link to='/searchcamera'> <NavItem onClick={() => setSelectedItem('camera')}><CameraIconNav /></NavItem></Link>
        <NavItem onClick={() => setSelectedItem('popular')}><FavoriteIconNav />Popular</NavItem>
        <NavItem onClick={() => setSelectedItem('profile')}><AccountIconNav />Profile</NavItem>
      </ NavigationBarContainer>
    )
  } else if (selectedItem === 'camera') {
    return (
      <NavigationBarContainer>
        <Link to='/searchbar'><NavItem onClick={() => setSelectedItem('saved')}><BookmarkIconNav />Saved</NavItem></Link>
        <Link to='/searchbar'><NavItem onClick={() => setSelectedItem('search')}><SearchIconNav />Search</NavItem></Link>
        <Link to='/searchcamera'><SelectedNavItem onClick={() => setSelectedItem('camera')}><Underline><CameraIconNav /></Underline></SelectedNavItem></Link>
        <NavItem onClick={() => setSelectedItem('popular')}><FavoriteIconNav />Popular</NavItem>
        <NavItem onClick={() => setSelectedItem('profile')}><AccountIconNav />Profile</NavItem>
      </ NavigationBarContainer>
    )
  } else if (selectedItem === 'popular') {
    return (
      <NavigationBarContainer>
        <Link to='/searchbar'><NavItem onClick={() => setSelectedItem('saved')}><BookmarkIconNav />Saved</NavItem></Link>
        <NavItem onClick={() => setSelectedItem('search')}><SearchIconNav />Search</NavItem>
        <NavItem onClick={() => setSelectedItem('camera')}><CameraIconNav /></NavItem>
        <Underline><SelectedNavItem onClick={() => setSelectedItem('popular')}><FavoriteIconNav />Popular</SelectedNavItem></Underline>
        <NavItem onClick={() => setSelectedItem('profile')}><AccountIconNav />Profile</NavItem>
      </ NavigationBarContainer>
    )
  } else {
    return (
      <NavigationBarContainer>
        <Link to='/searchbar'><NavItem onClick={() => setSelectedItem('saved')}><BookmarkIconNav />Saved</NavItem></Link>
        <Link to='/searchbar'><NavItem onClick={() => setSelectedItem('search')}><SearchIconNav />Search</NavItem></Link>
        <Link to='/searchcamera'><NavItem onClick={() => setSelectedItem('camera')}><CameraIconNav /></NavItem></Link>
        <NavItem onClick={() => setSelectedItem('popular')}><FavoriteIconNav />Popular</NavItem>
        <Underline><SelectedNavItem onClick={() => setSelectedItem('profile')}><AccountIconNav />Profile</SelectedNavItem></Underline>
      </ NavigationBarContainer>
    )
  }
}

export default NavigationBar
