import React, { useState, useEffect } from 'react'
// import { Card, Collapse, List, ListItem, Grid, Typography, Divider, ListItemText } from '@material-ui/core'
// import { ExpandLess, ExpandMore } from '@material-ui/icons/'
import axios from 'axios'

import PairCard from '../../shared/components/PairCard'
import { makeStyles } from '@material-ui/core/styles';
import NavigationBar from '../../shared/components/NavigationBar'
import history from '../../History'

const useStyles = makeStyles({
  root: {
    width: '100vw',
    backgroundColor: '#fff8e1'
  },
});



const Results = ({ match }) => {
  const classes = useStyles();
  //console.log(match.params)
  const [pairings, setPairings] = useState([])
  const type = match.params.type
  const searchTerm = match.params.term
  const barcode = match.params.barcode

  useEffect(() => {
    let url = ''
    if (match.params.barcode) {
      url = `${type}/getPairing/barcode/${barcode}`
    } else {
      // update search term
      url = `${type}/getPairing/${searchTerm}`
    }

    axios({
      method: 'get',
      url: url,
      baseURL: '/'
    })
      .then(response => {
        setPairings(response.data)
        console.log(response.data)
      })
      .catch(error => {
        console.log(error)
      })
  }, [])

  const [open, setOpen] = useState(true)

  const handleClick = () => {
    setOpen(!open)
  }

  const displayPairings = () => {
    if(pairings.length === 0)
    {
      return(<p>loading...</p>)
    }
    else
    {
      if(!pairings.error){
        console.log('These are the pairings:', pairings)
        return (pairings.map(pair => 
          <PairCard pair={pair} ></PairCard>
        ))
      }
      else{
        // If error, redirect back to searchbar for manual testing.
        history.push('/searchbar')  
      }
    }
  }

  
    return (
      
      <div class={classes.root}>
        <NavigationBar></NavigationBar>
        {displayPairings()}    
      </div>
    )
  
}

export default Results
